<template>
  <div class="eol-table-box-right">
    <div class="eol-table-box-right-inner" style="height:100%;padding:0px 10px;">
      <div class="eol-table-container box-shadow-style-2" style="height:100%;padding-top:10px;">
        <!--标签页-->
        <div class="tabs background-write">
          <ul>
            <li @click="tabChange('ContainerForm')" :class="{'active':'ContainerForm' == this.currentView}"> 
              <span>新建表单</span>
              <!-- <i class="line"></i> -->
            </li>
            <!-- <li @click="tabChange('BaseInfor')" :class="{'active':'BaseInfor' == this.currentView}">
              <span>流程引擎</span>
            </li> -->
          </ul>
        </div>

        <div class="bk10"></div>
  
        <component :is="currentView"></component>
      
      </div>
    </div>
  </div>
</template>

<script>
  import ContainerForm from "./ContainerForm.vue";//引入表单组件
  import BaseInfor from './BaseInfor.vue';//引入基本信息组件
  export default {
    data() {
      return {
        currentView:'ContainerForm',//默认自定义表单
      }
    },
    methods: {
      tabChange(item){
        this.currentView = item;
      }
    },
    mounted () {
    
    },
    computed: {},
    components:{
      ContainerForm,
      BaseInfor
    },
    watch: {
      widgetForm: {
        deep: true,
        handler: function (val) {
          console.log(this.$refs.widgetForm)
        }
      }
  }
  }
</script>
<style lang="less" scoped>
 
  .tabs{
    padding:0px 3px;
    height:55px;
    line-height: 55px;
    font-size:16px;
    cursor: pointer;
    overflow: hidden;
    ul{
      float: left;
      li{
        float: left;
        padding:0px 9px;
        position: relative;
        color:#7f7f7f;
        &.active{
          color:#097ed5;
        }
        .line{
          position: absolute;
          background-color: #ccc;
          width:2px;
          height:14px;
          right:0;
          top:20.5px;
        }
      }
    }
  }
</style>




