import Vue from 'vue';
import Vuex from 'Vuex';
import * as types from './mutation-types';
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    fromVisible: false,
    tableVisible:true,
    //控制表单模块的值
    formControlVisible: false,
    formTableVisible:true,
    // routeInfo:{}, // 面包屑导航显示内容,
    // username:localStorage.getItem('username') || '',
  },
  getters:{
    // 参数列表state指的是state数据
    getFromVisible(state){
        return state.fromVisible;
    },
    getRableVisible(state){
      return state.tableVisible;
    },
    //获取表单模块的值
    getformControlVisible(state){
      return state.formControlVisible;
    },
    getformTableVisible(state){
      return state.formTableVisible;
    },

  },
  mutations:{
    // state指的是state的数据
      // name传递过来的数据
    setFrom(state, name){
      state.fromVisible = name;//将传参设置给state的fromVisible
    },
    settRable(state, name){
      state.tableVisible = name;//将传参设置给state的tableVisible
    },
    //表单模块
    settFomrModelForm(state, name){
      state.formControlVisible = name;//将传参设置给state的fromVisible
    },
    settFomrModelRable(state, name){
      state.formTableVisible = name;//将传参设置给state的tableVisible
    },
   
  },
   // 通常跟api接口打交道
  actions:{
     // 设置城市信息
    // 参数列表：{commit, state}
    // state指的是state数据
    // commit调用mutations的方法 
    // name就是调用此方法时要传的参数
    setFromVisible({commit,state}, name){
      // 调用mutaions里面的方法
      commit("setFrom", name);
    },
    settRableVisible({commit,state}, name){
      // 调用mutaions里面的方法
      commit("settRable", name);
    },
    //表单模块
    setformControlVisible({commit,state}, name){
      // 调用mutaions里面的方法
      commit("settFomrModelForm", name);
    },
    settformTableVisible({commit,state}, name){
      // 调用mutaions里面的方法
      commit("settFomrModelRable", name);
    },

  },
})
export default store;



