
export function padLeftZero(str) {
  return ("00" + str).substr(str.length);
}
// 格式化成 年-月-日
// 'yyyy-MM-dd hh:mm:ss'
export function formatDate(date, fmt) {
  date = new Date(date);
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  }
  var o = {
    "M+": date.getMonth() + 1,
    "d+": date.getDate(),
    "h+": date.getHours(),
    "m+": date.getMinutes(),
    "s+": date.getSeconds()
  };
  for (var k in o) {
    if (new RegExp("(" + k + ")").test(fmt)) {
      var str = o[k] + "";
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? str : padLeftZero(str)
      );
    }
  }
  return fmt;
}
//获取当前日期
export function currentDate(num){
  let resultDate,year,month,date,hms;
  let currDate = new Date();
  year = currDate.getFullYear();
  month = currDate.getMonth()+ 1 + num;
  date = currDate.getDate();
  hms = currDate.getHours() + ':' + currDate.getMinutes() + ':' + (currDate.getSeconds() < 10 ? '0'+currDate.getSeconds() : currDate.getSeconds());
  switch(month)
  {
    case 13:
      month -= 12;
      year++;
      break;
    case 14:
      month -= 12;
      year++;
      break;
    case 15:
      month -= 12;
      year++;
      break;
    default:
      //month += 3;
      break;
  }
  month = (month < 10) ? ('0' + month) : month;
  date = (date < 10) ? ('0' + date) : date;
  resultDate = year.toString() +month.toString() + date.toString();
  return resultDate;
}
export function threeMonths(begintime,endtime){
  var time1 = new Date(begintime).getTime();
  var time2 = new Date(endtime).getTime();
  if(begintime==''){
    //alert("开始时间不能为空");
    return false;
  }
  if(endtime==''){
    //alert("结束时间不能为空");
    return false;
  }
  if(time1 > time2){
    //alert("开始时间不能大于结束时间");
    return false;
  }

  //判断时间跨度是否大于4个月
  var arr1 = begintime.split('-');
  var arr2 = endtime.split('-');
  arr1[1] = parseInt(arr1[1]);
  arr1[2] = parseInt(arr1[2]);
  arr2[1] = parseInt(arr2[1]);
  arr2[2] = parseInt(arr2[2]);
  var flag = true;
  if(arr1[0] == arr2[0]){//同年
    if(arr2[1]-arr1[1] > 4){ //月间隔超过4个月
      flag = false;
    }else if(arr2[1]-arr1[1] == 4){ //月相隔4个月，比较日
      if(arr2[2] > arr1[2]){ //结束日期的日大于开始日期的日
        flag = false;
      }
    }
  }else{ //不同年
    if(arr2[0] - arr1[0] > 1){
      flag = false;
    }else if(arr2[0] - arr1[0] == 1){
      if(arr1[1] < 10){ //开始年的月份小于10时，不需要跨年
        flag = false;
      }else if(arr1[1]+4-arr2[1] < 12){ //月相隔大于4个月
        flag = false;
      }else if(arr1[1]+4-arr2[1] == 12){ //月相隔4个月，比较日
        if(arr2[2] > arr1[2]){ //结束日期的日大于开始日期的日
          flag = false;
        }
      }
    }
  }
  if(!flag){
    //alert("时间跨度不得超过3个月！");
    return false;
  }
  return true;
}
