<template>
    <div class="baseInforForm background-write">
        <div id="form">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <div class="group-form-item" style="overflow:hidden;">
                    <el-form-item label="表单名称" prop="name" style="display:inline-block; float: left;">
                        <el-input v-model="ruleForm.name" style="width:270px;"></el-input>
                    </el-form-item>
                    <el-form-item label="类型" prop="name" style="display:inline-block; float: right;">
                        <el-input v-model="ruleForm.name" style="width:270px;"></el-input>
                    </el-form-item>
                </div>
                <div class="group-form-item" style="overflow:hidden;">
                    <el-form-item label="所属流程" style="display:inline-block; float: left;">
                        <el-select v-model="ruleForm.region" placeholder="请选择" style="width:270px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="版本号" style="display:inline-block; float: right;">
                        <el-select v-model="ruleForm.region" placeholder="请选择" style="width:270px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="group-form-item" style="overflow:hidden;">
                    <el-form-item label="创建人" style="display:inline-block; float: left;">
                        <el-select v-model="ruleForm.region" placeholder="请选择" style="width:270px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="创建时间" style="display:inline-block; float: right;">
                        <el-select v-model="ruleForm.region" placeholder="请选择" style="width:270px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="group-form-item" style="overflow:hidden;">
                    <el-form-item label="活动形式">
                        <el-input type="textarea" v-model="ruleForm.desc" :rows="7"></el-input>
                    </el-form-item>
                </div>
                
                <div class="submit-but">
                    <el-button size="medium" type="primary" @click="submitForm('ruleForm')">保存</el-button>
                    <el-button size="medium" @click="resetForm('ruleForm')">取消</el-button>
                </div>

               
            </el-form>
        </div>
       
    </div>
</template>
<script>
    export default {
        data() {
            return {
                ruleForm: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
                date1: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                date2: [
                    { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                ],
                type: [
                    { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                ],
                resource: [
                    { required: true, message: '请选择活动资源', trigger: 'change' }
                ],
                desc: [
                    { required: true, message: '请填写活动形式', trigger: 'blur' }
                ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>
<style lang="less" scoped>
    .baseInforForm{
        padding:50px 24px;
        height: 100%;
         #form{
            width:50%;
            margin:0 100px;
            .submit-but{
                padding:20px 0px;
                text-align: center;
            }
        }
    }
   
</style>





