import Vue from 'vue'
import Router from 'vue-router'

//首页
let Home = () => import('../pages/Home/index.vue');//首页
let statisticalReport = () => import('../pages/Home/statisticalReport/index.vue');



//流程管理
let ProcessManage = () => import('../pages/ProcessManage/index.vue');
let ProcessInFormation = () => import('../pages/ProcessManage/ProcessInFormation/index.vue');
let InstanceMonitoring = () => import('../pages/ProcessManage/InstanceMonitoring/index.vue');
let TaskCenter = () => import('../pages/ProcessManage/TaskCenter/index.vue');

//模型管理
import ModelManage from '../pages/ModelManage/index.vue';//模型管理
import NewModel from '../pages/ModelManage/NewModel/index.vue';//新建模型
import DditModel from '../pages/ModelManage/DditModel/index.vue';//编辑模型
import DelModel from '../pages/ModelManage/DelModel/index.vue';//删除模型
import DeploymentModel from '../pages/ModelManage/DeploymentModel/index.vue';//部署模型
import ModelImport from '../pages/ModelManage/ModelImport/index.vue';//模型导入
import ModelExport from '../pages/ModelManage/ModelExport/index.vue';//模型导出

//系统管理
import SystemManage from '../pages/SystemManage/index.vue'//系统管理
import OrganizatStructure from '../pages/SystemManage/OrganizatStructure/index.vue';//组织结构
import RoleManage from '../pages/SystemManage/RoleManage/index.vue'//角色管理
import UserManage from '../pages/SystemManage/UserManage/index.vue'//用户管理
import ShopCalendar from '../pages/SystemManage/ShopCalendar/index.vue'//工作日历
import DigitalDictionary from '../pages/SystemManage/DigitalDictionary/index.vue'//数字字典
import LabelManage from '../pages/SystemManage/LabelManage/index.vue'//标签管理
import SystemLog from '../pages/SystemManage/SystemLog/index.vue'//系统日志
import TenantManage from '../pages/SystemManage/TenantManage/index.vue'//租户管理
import PrivilegeManage from '../pages/SystemManage/PrivilegeManage/index.vue'//权限管理
import DataInitializat from '../pages/SystemManage/DataInitializat/index.vue'//数据化初始化
import IntegrateConfigurat from '../pages/SystemManage/IntegrateConfigurat/index.vue'//集成配置

//表单管理模块
import FormManage from '../pages/FormManage/index.vue'
import FormControl from '../pages/FormManage/FormControl/index.vue'//表单控制自定义组件
import FormTable from '../pages/FormManage/FormTable/formtable.vue';//表单控制主页面

import Login from '../pages/Login/index.vue';
import App from '../pages/App.vue';

Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        {
          path: '/', //
          component: Home,
          redirect:'Home',
        },
        //首页
        {
          path: '/Home', //首页
          component: Home,
          //首页子菜单
          children: [
            {
              path: '',
              component: statisticalReport,
            
            }
          ]
        },
        //流程管理
        {
          path: '/processManage', 
          component: ProcessManage,
          //流程管理子菜单
          children: [
            //流程信息
            {
              path: '',
              component: ProcessInFormation,
            },
            //实例监控
            {
              path: 'instanceMonitoring',
              component: InstanceMonitoring,
            },
            //实例监控
            {
              path: 'taskCenter',
              component: TaskCenter,
            }
          ]
        },
        //模型管理
        {
          path: '/modelManage', 
          component: ModelManage,
          meta: {
            routeInfo: [
              { name: '模型管理', path: '/ModelManage', },
              { name: '模型管理页面', path: '/ModelManage' }
            ]
          },
          children: [
            //新建模型
            {
              path: '/',
              component: NewModel,
              meta: {
                routeInfo: [
                  { name: '模型管理', path: '/ModelManage', },
                  { name: '新建模型', path: '/ModelManage' }
                ]
              },
            },
            //编辑模型
            {
              path: 'editModel',
              component: DditModel,
            },
            //删除模型
            {
              path: 'delModel',
              component: DelModel,
            },
            //部署模型
            {
              path: 'deploymentModel',
              component: DeploymentModel,
            },
            //模型导入
            {
              path: 'modelImport',
              component: ModelImport,
            },
            //模型导出
            {
              path: 'modelExport',
              component: ModelExport,
            }
          ]
        },
        //系统管理
        {
          path: '/systemManage', //系统管理
          component: SystemManage,
          children: [
            //组织机构
            {
              path: '',
              component: OrganizatStructure,
            },
            //角色管理
            {
              path: 'roleManage',
              component: RoleManage,
            },
            //用户管理
            {
              path: 'userManage',
              component: UserManage,
            },
            //工作日历
            {
              path: 'shopCalendar',
              component: ShopCalendar,
            },
            //数字字典
            {
              path: 'digitalDictionary',
              component: DigitalDictionary,
            },
            //标签管理
            {
              path: 'labelManage',
              component: LabelManage,
            },
            //系统日志
            {
              path: 'systemLog',
              component: SystemLog,
            },
            //租户管理
            {
              path: 'tenantManage',
              component: TenantManage,
            },
            //权限管理
            {
              path: 'privilegeManage',
              component: PrivilegeManage,
            },
            //数据化初始化
            {
              path: 'dataInitializat',
              component: DataInitializat,
            },
            //集成配置
            {
              path: 'integrateConfigurat',
              component: IntegrateConfigurat,
            }
          ]
        },
        //表单管理
        {
          path: '/formManage', 
          component: FormManage,
          children: [
            {
              path: '',
              component: FormTable,
            }
          ]
        },
      ]
    },

    {
      path: '/login',
      component: Login
    }
  ],
  linkActiveClass: 'visited'
});



