<template>
  <el-container>
    <!--左栏-->
    <el-aside style="wdith: 250px;">
      <div class="components-list">
        
        <div class="widget-cate">
            <ul class="uls">
              <li :class="{active:this.leftControl == 'layoutControl'}" @click="handleLayoutControl('layoutControl')">
                <i class="w-line"></i>
                <span>字段控件</span>
              </li>
              <i class="h-line"></i>
              <li :class="{active:this.leftControl == 'FieldControl'}"  @click="handleLayoutControl('FieldControl')">
                <i class="w-line"></i>
                <span>布局控件</span>
              </li>
            </ul>
        </div>

        <!--对应tab切换的控件组件-->
        <div class="uls-draggable">
          <!--布局控件-->
          <draggable element="ul" :list="basicComponents" 
            :options="{group:{ name:'people', pull:'clone',put:false},sort:false, ghostClass: 'ghost'}"
            @end="handleMoveEnd"
            @start="handleMoveStart"
            :move="handleMove"
            v-show="this.leftControl == 'layoutControl'"
          >
            <li class="form-edit-widget-label" v-for="(item, index) in basicComponents" :key="index">
              <a>
                <icon class="icon" :name="item.icon"></icon>
                <span>{{item.name}}</span>
              </a>
            </li>
          </draggable>
        </div>

        <!--字段控件-->
        <draggable element="ul" :list="layoutComponents" 
          :options="{group:{ name:'people', pull:'clone',put:false},sort:false, ghostClass: 'ghost'}"
          @end="handleMoveEnd"
          @start="handleMoveStart"
          :move="handleMove"
          v-show="this.leftControl == 'FieldControl'"
        >
          
          <li class="form-edit-widget-label data-grid" v-for="(item, index) in layoutComponents" :key="index">
            <a>
              <icon class="icon" :name="item.icon"></icon>
              <span>{{item.name}}</span>
            </a>
          </li>
        </draggable>

      </div>
    </el-aside>

    <div class="height-w"></div>

    <!--中间-->
    <el-container class="center-container" direction="vertical" style="height:100%;">
      <el-header class="btn-bar form-title" style="height: 58px;line-height:58px;">
        <!--表单控制器图标-->
        <div class="form-titleImg">
          <span class="form-center-text">表单编辑区</span>
        </div>
        <el-button type="text" size="medium" icon="el-icon-tickets" @click="handleReturn">返回</el-button>
        <el-button type="text" size="medium" icon="el-icon-tickets" @click="handleClear">清空</el-button>
        <el-button type="text" size="medium" icon="el-icon-view" @click="handlePreview">预览</el-button>
        <!-- <el-button type="text" size="medium" icon="el-icon-tickets" @click="handleGenerateJson">生成JSON</el-button>
        <el-button type="text" size="medium" icon="el-icon-document" @click="handleGenerateCode">生成代码</el-button> -->
        <el-button type="text" size="medium" icon="el-icon-view" @click="handleSave">保存</el-button>
      </el-header>
      <el-main :class="{'widget-empty': widgetForm.list.length == 0}">
        <WidgetForm ref="widgetForm" :data="widgetForm" :select.sync="widgetFormSelect"></WidgetForm>
      </el-main>
    </el-container>

    <div class="height-w"></div>

    <!--右栏-->
    <el-aside class="widget-config-container">
      <el-container style="height:100%;">
        <el-header height="58px">
          <!-- /*标签页切换按钮*/ -->
          <div class="config-tab" 
          :class="{active: configTab=='widget'}" 
          @click="handleConfigSelect('widget')">
            <span>控件设置 </span>
            <i class="w-line"></i>
          </div>
          <i class="h-line"></i>
          <div class="config-tab" 
          :class="{active: configTab=='form'}" 
          @click="handleConfigSelect('form')">
            <span>表单设置</span>
            <i class="w-line"></i>
          </div>
        </el-header>
        <el-main class="config-content">
          <!-- /*字段属性*/ -->
          <WidgetConfig v-show="configTab=='widget'" :data="widgetFormSelect"></WidgetConfig>
          <!-- /*表单属性*/ -->
          <FormConfig v-show="configTab=='form'" :data="widgetForm.config"></FormConfig>
        </el-main>
      </el-container>
      
    </el-aside>

    <cus-dialog
      :visible="previewVisible"
      @on-close="previewVisible = false"
      ref="widgetPreview"
      @on-submit="handleTest"
      width="1000px"
      form
    >
      <generate-form v-if="previewVisible" :data="widgetForm" :remote="remoteFuncs" :value="widgetModels" ref="generateForm">

        <template slot="blank" slot-scope="scope">
          宽度：<el-input v-model="scope.model.blank.width" style="width: 100px"></el-input>
          高度：<el-input v-model="scope.model.blank.height" style="width: 100px"></el-input>
        </template>
      </generate-form>
    </cus-dialog>

    <cus-dialog
      :visible="jsonVisible"
      @on-close="jsonVisible = false"
      ref="jsonPreview"
      width="800px"
      form
    >
      <div id="jsoneditor" style="height: 400px;width: 100%;">{{jsonTemplate}}</div>
      
      <template slot="action">
        <el-button id="copybtn" data-clipboard-target=".ace_text-input">双击复制</el-button>
      </template>
    </cus-dialog>

    <cus-dialog
      :visible="codeVisible"
      @on-close="codeVisible = false"
      ref="codePreview"
      width="800px"
      form
      :action="false"
    >
      <div id="codeeditor" style="height: 500px; width: 100%;">{{htmlTemplate}}</div>
    </cus-dialog>
  </el-container>
</template>

<script>
import Draggable from 'vuedraggable'
import WidgetConfig from './WidgetConfig';//右栏字段属性
import FormConfig from './FormConfig';//右栏表单属性
import WidgetForm from './WidgetForm';//中间区域
import CusDialog from './CusDialog';//弹窗
import GenerateForm from './GenerateForm';
// import JSONEditor from 'jsoneditor'
// import 'jsoneditor/dist/jsoneditor.min.css'
import Clipboard from 'clipboard';//复制剪切板
import {basicComponents,layoutComponents} from './componentsConfig.js'
// import {loadJs, loadCss} from '../util/index.js';//加载js,加载css
// import request from '../util/request.js';//请求
// import generateCode from './generateCode.js'

export default {
  name: 'fm-making-form',
  components: {
    Draggable,
    WidgetConfig,
    FormConfig,
    WidgetForm,
    CusDialog,
    GenerateForm
  },
  data () {
    return {
      basicComponents,//基础字段数据
      layoutComponents,
      //控制初始状态表单的行向布局显示数据
      widgetForm: {
        list: [],
        config: {
          labelWidth: 100,
          labelPosition: 'right'
        },
      },
      leftControl:'layoutControl',//控件表单左栏切换组件
      configTab: 'widget',
      widgetFormSelect: null,
      previewVisible: false,
      jsonVisible: false,
      codeVisible: false,
      remoteFuncs: {
        func_test (resolve) {
          setTimeout(() => {
            const options = [
              {id: '1', name: '1111'},
              {id: '2', name: '2222'},
              {id: '3', name: '3333'}
            ]

            resolve(options)
          }, 2000)
        },
        funcGetToken (resolve) {
          request.get('http://tools-server.xiaoyaoji.cn/api/uptoken').then(res => {
            resolve(res.uptoken)
          })
        }
      },
      widgetModels: {},
      blank: '',
      htmlTemplate: '',
      jsonTemplate: ''
    }
  },
  mounted () {
    // loadCss('https://unpkg.com/jsoneditor/dist/jsoneditor.min.css')
    // loadJs('https://unpkg.com/jsoneditor/dist/jsoneditor.min.js')
    // loadJs('lib/ace/src/ace.js')
  },
  methods: {
    //返回
    handleReturn(){
       // 调用vuex的ations设置的值
      this.$store.dispatch("setformControlVisible", false);
      this.$store.dispatch("settformTableVisible", true);
    },
    //清空方法
    handleClear () {
      this.widgetForm.list = [];
    },
    //保存方法
    handleSave(){

    },
    //左栏控件切换方法
    handleLayoutControl(val){
      this.leftControl = val;
    },
    //字段属性方法
    handleConfigSelect (value) {
      this.configTab = value
    },
    handleMoveEnd (evt) {
      console.log('end', evt)
    },
    handleMoveStart ({oldIndex}) {
      console.log('start', oldIndex, this.basicComponents)
    },
    handleMove () {
      return true
    },
    //预览
    handlePreview () {
      this.previewVisible = true
    },
    handleTest () {
      // this.$refs.generateForm.getData().then(data => {
      //   this.$alert(data, '').catch(e=>{})
      //   this.$refs.widgetPreview.end()
      // }).catch(e => {
      //   this.$refs.widgetPreview.end()
      // })
    },
    //生成JSON
    handleGenerateJson () {
      this.jsonVisible = true
      // this.jsonTemplate = this.widgetForm
      // this.$nextTick(() => {

      //   const editor = ace.edit('jsoneditor')
      //   editor.session.setMode("ace/mode/json")

      //   const btnCopy = new Clipboard('#copybtn')
      // })
    },
    //生成代码
    handleGenerateCode () {
      this.codeVisible = true
      // this.htmlTemplate = generateCode(JSON.stringify(this.widgetForm))
      // this.$nextTick(() => {
      //   const editor = ace.edit('codeeditor')
      //   editor.session.setMode("ace/mode/html")
      // })
    }
  },
  watch: {
    widgetForm: {
      deep: true,
      handler: function (val) {
        console.log(this.$refs.widgetForm)
      }
    }
  }
}
</script>

<style lang="less">
  @import url('../../../styles/index.less');
  @import url('../../../styles/cover.less');
  
  .form-title{
    position: relative;
     .form-titleImg{
      position: absolute;
      top:0px;
      left:-5px;
      display: inline-block;
      background: url('../../../assets/images/form-title.png') no-repeat;
      width: 92px;
      height: 32px;
      line-height: 28px;
      text-align: center;
      font-size: 12px;
      .form-center-text{
        color:#fff;
      }
    }
  }
 

  .widget-empty{
    // background: url('../assets/form_empty.png') no-repeat;
    background-position: 50%;
  }

</style>



