<template>
  <div class="cont_box">
			<div class="nav_box">
				<span class="nav_line"></span>
				<b>EOL管理系统</b>
			</div>
			<div class="mainBox">
				<div class="loginBox">
					<h3 class="loginTitle">欢迎登陆</h3>
					<div class="panel_item">
						<input type="text" class="inpt " v-model="username" placeholder="用户名"/>
					</div>
					<div class="panel_item">
						<input type="password" class="inpt" v-model="password" value="" placeholder="密码"/>
					</div>
					<div class="remberpast_box">
						<div class="check_box" @click="flag=!flag">
					
							<input type="checkbox" name="remberPast" id="remberPast" />
						</div>
						<label for="remberPast">记住密码</label>
					</div>
					<div class="panel_item">
						<button class="login_btn" @click="login">登录</button>
					</div>
				</div>
			</div>
			<div class="copyRight_box">
				Copyright©2018京东方科技集团股份有限公司 版权所有
			</div>
		</div>
</template>
<script>
import $ from "jquery";
import axios from 'axios'
import  {mapMutations} from 'Vuex';
import * as types from  '@/store/mutation-types';
export default {
  data(){
    return {
      flag:true,
      username:'',
      password:''
    }
  },
  mounted() {
    this.setWindowHeight();
  },
  methods: {
    ...mapMutations([types.CHANGE_USERNAME]),
    setWindowHeight() {
      let totalHeight = $(window).height();
      $(".mainBox").height(totalHeight - 140);
    },
    login() {
      let url = `/oauth/token?grant_type=password&username=${this.username}&password=${this.password}`;
      axios({
        method:'post',
        url,
        headers:{
          "Authorization":"Basic ZW9sOmVvbFNlY3JldA=="
        }
      }).then((res)=>{
        let {token} = res.data;
        // 保存token
        localStorage.setItem('token',`bearer ${token}`);
        // 保存用户名
        this[types.CHANGE_USERNAME](this.username);
        this.$router.push('/home');
      },err=>{
        console.log(err);
      })
    }
  }
};
</script>
<style>
.cont_box {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
/*头部*/
.nav_box {
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: #ffffff;
}
.nav_logo {
  margin-left: 42px;
  height: 32px;
}
.nav_line {
  display: inline-block;
  width: 0;
  height: 56%;
  margin: 0 6px;
  border: 1px solid #f9f9f9;
  vertical-align: middle;
}
.nav_box b {
  color: #4e4e4e;
  font-size: 16px;
}
.mainBox {
  width: 100%;
  overflow: hidden;
  position: relative;
}
.mainBox img {
  width: 100%;
  height: 100%;
}
.mainBox .loginBox {
  width: 250px;
  height: 250px;
  position: absolute;
  right: 10%;
  top: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  background: #ffffff;
}
.loginBox .loginTitle {
  background: #f5f5f5;
  height: 36px;
  line-height: 36px;
  font-size: 16px;
}
.loginBox .panel_item {
  margin-top: 20px;
}
.loginBox .remberpast_box {
  text-align: left;
  width: 198px;
  margin: auto;
  margin-top: 6px;
}
.remberpast_box .check_box {
  display: inline-block;
  width: 13px;
  height: 13px;
  position: relative;
  vertical-align: middle;
}
.remberpast_box label {
  font-size: 12px;
  color: #333333;
}
.remberpast_box .check_box img {
  width: 100%;
  height: 100%;
  vertical-align: top;
}
.remberpast_box .check_box input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
}
.loginBox .panel_item .inpt {
  width: 172px;
  height: 28px;
  line-height: 28px;
  padding-left: 24px;
  border: 1px solid #cacaca;
}


.loginBox .panel_item .login_btn {
  width: 198px;
  height: 30px;
  color: #ffffff;
  line-height: 30px;
  background: #218ae4;
  border: 1px solid #218ae4;
}
.loginBox .panel_item .login_btn:hover {
  cursor: pointer;
}
.copyRight_box {
  text-align: center;
  height: 80px;
  line-height: 80px;
}
</style>



