<template>
      <div class="eol-table-box-right">
        <div class="eol-table-box-right-inner">
          <div class="eol-table-container box-shadow-style-2">
            <!--模型表格-->
            <div class="modelTable" v-show="tableVisible">
              <!--表单-->
              <div id="form-model">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                  <el-form-item label="流程ID">
                    <el-input v-model="formInline.id" placeholder="请输入ID"></el-input>
                  </el-form-item>
                  <el-form-item label="流程名称">
                    <el-select v-model="formInline.region" placeholder="请选择名称">
                      <el-option label="流程名称1" value="shanghai"></el-option>
                      <el-option label="流程名称2" value="beijing"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" size="small  " @click="onSubmit">查询</el-button>
                  </el-form-item>
                </el-form>
              </div>

              <!--按钮-->
              <div class="model-btn">
                <el-button type="primary" size="small" @click="isShowTable">新建</el-button>
                <el-button type="primary" size="small">导入</el-button>
              </div>

              <!--表格-->
              <div class="modelList-table" style="">
                  <template>
                    <el-table
                      ref="multipleTable"
                      :data="tableData3"
                      tooltip-effect="dark"
                      max-height="700"
                      style="width: 100%"
                      @selection-change="handleSelectionChange">
                      <el-table-column
                        type="selection"
                        align="center"
                        width="60">
                      </el-table-column>
                      <el-table-column
                        label="ID"
                        align="center"
                        width="120">
                        <template slot-scope="scope">{{ scope.row.id }}</template>
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="名称"
                        align="center"
                        width="120">
                      </el-table-column>
                      <el-table-column
                        prop="type"
                        label="类型"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="versionNumber"
                        label="版本号"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="graphical"
                        label="流程图形"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="deploymentTime"
                        label="部署时间"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        label="操作"
                        width="180"
                        align="center">
                        <template>
                          <img class="margin-right-3px pointer" src="../../../assets/images/edit.png" alt="编辑">
                          <img class="margin-right-3px pointer" src="../../../assets/images/deploy.png" alt="部署">
                          <img class="margin-right-3px pointer" src="../../../assets/images/import.png" alt="导入">
                          <img class="margin-right-3px pointer" src="../../../assets/images/export.png" alt="导出">
                          <img class="pointer" src="../../../assets/images/delete.png" alt="删除">
                        </template>
                      </el-table-column>
                    </el-table>
                  </template>
              </div>

              <!--分页-->
              <div class="pagination-block" v-show="tableVisible">
                    <el-pagination
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page="currentPage"
                      :page-sizes="[100, 200, 300, 400]"
                      :page-size="100"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="400">
                    </el-pagination>
              </div>

            </div>

            <!--流程引擎组件-->
            <div class="processEngine" v-show="fromVisible">
               <!--按钮-->
              <div class="processEngineModel-btn">
                <!-- <el-button type="primary"  size="small" @click="isShowTable">新建模板</el-button> -->
                <el-button type="primary"  size="small" @click="retrunTable">返回上一级</el-button>
              </div>

              <div class="iframe" style="padding:0px 20px;height:100%;">
                <!-- <iframe ref="iframe"  src="../../../../static/processDesigner/index.html" width="100%" height="100%"></iframe> -->
                <iframe ref="iframe" src="/workflow/front-resource/processDesigner/index.html" width="100%" height="100%"></iframe>
              </div>
            </div>

          </div>
          
        </div>
      </div>
</template>


<script>
  
  export default {
    components:{
    
    },
    data() {
      return {
        currentPage: 1,
        formInline: {
          id: '',
          region: ''
        },
        //表格数据
        tableData3: [{
          id: '001',
          name: 'B5企划流程',
          type: '审核中',
          versionNumber:'版本1',
          graphical:'图形01',
          deploymentTime:'2019-2-15',
        },{
          id: '002',
          name: 'B4企划流程',
          type: '审核成功',
          versionNumber:'版本2',
          graphical:'图形02',
          deploymentTime:'2019-2-18',
        },{
          id: '003',
          name: 'B5企划流程',
          type: '审核失败',
          versionNumber:'版本1',
          graphical:'图形01',
          deploymentTime:'2019-2-15',
        },{
          id: '004',
          name: 'B5企划流程',
          type: '审核成功',
          versionNumber:'版本1',
          graphical:'图形01',
          deploymentTime:'2019-2-16',
        },{
          id: '005',
          name: 'B5企划流程',
          type: '审核中',
          versionNumber:'版本4',
          graphical:'图形05',
          deploymentTime:'2019-2-15',
        }, {
          id: '006',
          name: 'B5企划流程',
          type: '审核中',
          versionNumber:'版本1',
          graphical:'图形06',
          deploymentTime:'2019-2-17',
        }, {
          id: '007',
          name: 'B5企划流程',
          type: '审核失败',
          versionNumber:'版本6',
          graphical:'图形07',
          deploymentTime:'2019-2-15',
        },{
          id: '008',
          name: 'B5企划流程',
          type: '审核中',
          versionNumber:'版本6',
          graphical:'图形01',
          deploymentTime:'2019-2-20',
        },{
          id: '009',
          name: 'B5企划流程',
          type: '审核成功',
          versionNumber:'版本4',
          graphical:'图形01',
          deploymentTime:'2019-2-27',
        },{
          id: '010',
          name: 'B5企划流程',
          type: '审核中',
          versionNumber:'版本2',
          graphical:'图形5',
          deploymentTime:'2019-2-15',
        },],
        //全选反选
        multipleSelection: []
      };
    },
    mounted() {
      
    },
    computed:{
      fromVisible:function() {
        // 通过vuex的getters方法来获取state里面的数据
        return this.$store.getters.getFromVisible;
      },
      tableVisible:function() {
        // 通过vuex的getters方法来获取state里面的数据
        return this.$store.getters.getRableVisible;
      },

    },
    methods: {
      //返回上一级
      retrunTable(){
        //更改vuex中的值
        // 调用vuex的ations设置的值
        this.$store.dispatch("setFromVisible", false);
        this.$store.dispatch("settRableVisible", true);
      },
      //显示隐藏表格流程图
      isShowTable(){
        // 调用vuex的ations设置的值
        this.$store.dispatch("setFromVisible", true);
        this.$store.dispatch("settRableVisible", false);
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
  };
</script>


<style lang="less" scoped>
  .processEngine{
    width:100%;
    height: calc(100% - 65px);
    padding-top:10px;
    //流程引擎按钮
    .processEngineModel-btn{
      margin:0px 20px 10px 20px;
    }
  }
  .eol-table-box-right,
  .eol-table-box-right-inner,
  .eol-table-container{
      height:100%;
  }
  #form-model{
    padding:20px 20px 0px 20px;
    border-bottom: 1px solid #BAC4CC;
  }
  
  .modelTable{
    //按钮
    .model-btn{
      padding:10px 20px;
    }
    
    //表格
    .modelList-table{
      padding:0px 20px;
    }
    //分页
    .pagination-block{
      margin:20px;
      padding:10px 0px;
      text-align: center;
      background: #e5f5fd;
    }
  }
</style>



