<template>
  <div class="eol-table-box-left-inner">
    <div class="eol-left-menu-title" v-for="(menu,index) in menuData" :key="index">
      <div class="fl menu-title-word">
        <img class="menu-icon" :src="menu.icon" />
        {{menu.title}}
      </div>
      <div class="fr menu-translate-icon translate-keyframe-style-1" id="left-menu-translate" data-normal="2">
       
      </div>
      <div class="clear"></div>
        <div v-show="menu.isShow" class="eol-left-menu-list-box" id="left-menu-list-box">
        <ul>
            <router-link :to="c.path" :exact="true" tag="li" v-for="(c,index) in menu.children" :key="index">
              <a style="vertical-align: middle;">
                <!-- <img :src="c.icon" /> -->
                <span :class="c.icon" style="vertical-align: middle;font-size:18px;margin-right: 10px;"></span>
                {{c.title}}
              </a>
            </router-link>
          </ul>
        </div>
    </div>

  </div>
</template>
<script>
export default {
  props: ['menuData'],
};
</script>

<style scoped>
.eol-table-box-left-inner {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  width: 200px;
}
</style>



