import axios from 'axios';
import router from '../router/index'
// axios的拦截器
let baseURL = '';
let bootUrl = '';

if (process.env.NODE_ENV === 'development'){
  // baseURL = 'http://47.92.38.112:8080';
  // bootUrl = 'http://47.92.38.112:8080'
  bootUrl = 'http://2u2e615543.iok.la/';
  baseURL = 'http://2u2e615543.iok.la/';
}else{
  baseURL = 'http://localhost:8080';
  bootUrl = 'http://localhost:8080'; //正式ip地址
}

axios.defaults.baseURL = baseURL;

export { baseURL,bootUrl}
export function formatData(obj) {
  var arr = [];
  for (var key in obj) {
    arr.push(key + "=" + obj[key]);
  }
  return arr.join("&");
}
axios.interceptors.request.use(function (config) {
  if (config.headers['Content-Type'] === 'multipart/form-data'){
    return config
  }


  if(typeof config.data !== 'string'){
    config.data = formatData(config.data);
  }

  if (!config.headers.Authorization){
    config.headers = {
      "Content-Type":"application/x-www-form-urlencoded",
      "Authorization": localStorage.getItem('token')
    }
  }
  return config
});
axios.interceptors.response.use(function (res) {
  // 如果code 为1    res.data = {code:1,data:{rows}]
  if (res.data.access_token){
    return res.data
  }
  if (res.data.code === 1) {
    return res.data;
  } else {
    // 不为1就是失败
    return Promise.reject();
  }
}, res=> {
  if(res.response.status === 401){ // 没权限
      router.history.push('/login');
  }
  return Promise.reject();
});
export default axios



