<template>
      <div class="eol-table-box-right">
        <div class="eol-table-box-right-inner">
          <div class="eol-table-container box-shadow-style-2">
            <div class="formTable" v-show="formTableVisible">
                <!--表单-->
                <div id="form-model">
                    <el-form :inline="true" :model="formInline" class="demo-form-inline">
                        <el-form-item label="标题">
                        <el-input v-model="formInline.id" placeholder="请输入标题"></el-input>
                        </el-form-item>
                        <el-form-item label="类型">
                        <el-select v-model="formInline.region" placeholder="请选择类型">
                            <el-option label="类型1" value="shanghai"></el-option>
                            <el-option label="类型2" value="beijing"></el-option>
                        </el-select>
                        </el-form-item>
                        <el-form-item>
                        <el-button type="primary" size="small  " @click="onSubmit">查询</el-button>
                        </el-form-item>
                    </el-form>
                </div>

                <!--按钮-->
                <div class="model-btn">
                <el-button type="primary" size="small" @click="isShowTable">新建表单</el-button>
                <!-- <el-button type="primary" size="small">导入</el-button> -->
                </div>

                <!--表格-->
                <div class="modelList-table">
                    <template>
                        <el-table
                        ref="multipleTable"
                        :data="tableData3"
                        tooltip-effect="dark"
                        max-height="700"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                        <el-table-column
                            label="标题"
                            width="320"
                            align="center">
                            <template slot-scope="scope">{{ scope.row.title }}</template>
                        </el-table-column>
                        <el-table-column
                            prop="processName"
                            label="描述"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="processName"
                            label="类型"
                            width="250"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="status"
                            label="状态"
                            width="250"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            label="操作"
                            width="200"
                            align="center">
                            <template>
                                <img class="margin-right-3px pointer" src="../../../assets/images/edit.png" alt="编辑">
                                <img class="margin-right-3px pointer" src="../../../assets/images/deploy.png" alt="部署">
                                <img class="margin-right-3px pointer" src="../../../assets/images/import.png" alt="导入">
                                <img class="margin-right-3px pointer" src="../../../assets/images/export.png" alt="导出">
                                <img class="pointer" src="../../../assets/images/delete.png" alt="删除">
                            </template>
                        </el-table-column>
                        </el-table>
                    </template>
                
                    
                </div>

                <!--分页-->
                <div class="pagination-block" >
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[100, 200, 300, 400]"
                        :page-size="100"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="400">
                    </el-pagination>
                </div>
            </div>

            <!--自定义表单组件-->
            <formControlComponenet v-show="formControlVisible"></formControlComponenet>
          
          </div>
          
        </div>
      </div>
</template>


<script>
  import formControlComponenet from '../FormControl/index.vue';
  export default {
    components:{
        formControlComponenet
    },
    data() {
      return {
        currentPage: 1,
        formInline: {
          id: '',
          region: ''
        },
        //表格数据
        tableData3: [{
          title: 'B5企划流程',
          processName: 'test1',
          nodeName:'结束',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B6企划流程',
          processName: 'test1',
          nodeName:'担当审批',
          status:'结束',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B8企划流程',
          processName: 'test1',
          nodeName:'担当审批',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B5企划流程',
          processName: 'test1',
          nodeName:'结束',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B4企划流程',
          processName: 'test5',
          nodeName:'担当审批',
          status:'结束',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B5企划流程',
          processName: 'test1',
          nodeName:'结束',
          status:'结束',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B5企划流程',
          processName: 'test2',
          nodeName:'结束',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B1企划流程',
          processName: 'test1',
          nodeName:'担当审批',
          status:'结束',
          processClassificat:'test3',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B5企划流程',
          processName: 'test1',
          nodeName:'结束',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        },{
          title: 'B5企划流程',
          processName: 'test3',
          nodeName:'担当审批',
          status:'正常',
          processClassificat:'test',
          versionNumber:'版本1',
          graphical:'图形01',
          example:'实例1',
          creationTime:'2019-2-15',
        }],
        //全选反选
        multipleSelection: []
      };
    },
    mounted() {
      
    },
    computed:{
      formControlVisible:function() {
        // 通过vuex的getters方法来获取state里面的数据
        return this.$store.getters.getformControlVisible;
      },
      formTableVisible:function() {
        // 通过vuex的getters方法来获取state里面的数据
        return this.$store.getters.getformTableVisible;
      },

    },
    methods: {
      //显示隐藏表格流程图
      isShowTable(){
        // 调用vuex的ations设置的值
        this.$store.dispatch("setformControlVisible", true);
        this.$store.dispatch("settformTableVisible", false);
      },
      
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
  };
</script>


<style lang="less" scoped>

  .eol-table-box-right,
  .eol-table-box-right-inner,
  .eol-table-container{
      height:100%;
  }
  #form-model{
    padding:20px 20px 0px 20px;
    border-bottom: 1px solid #BAC4CC;
  }
  //按钮
  .model-btn{
    padding:10px 20px;
  }
  //表格
  .modelList-table{
     padding:0px 20px;
  }
  //分页
  .pagination-block{
    margin:20px;
    padding:10px 0px;
    text-align: center;
    background: #e5f5fd;
  }
</style>



